.movie-card {
  color: #fff;
  background-color: #222;
  border: none;
  border-radius: 0 0 10px 10px;
  flex-direction: column;
  max-width: 300px;
  height: 100%;
  max-height: 600px;
  transition: transform .2s ease-in-out, box-shadow .2s ease-in-out;
  display: flex;
}

.movie-card:hover {
  transform: scale(1.05);
  box-shadow: 0 8px 16px #0003;
}

.movie-card .card-img-top {
  object-fit: cover;
  border-radius: 10px 10px 0 0;
  width: 100%;
  height: 300px;
}

.movie-card .card-body {
  flex-direction: column;
  flex-grow: 1;
  justify-content: space-between;
  padding: 1rem;
  display: flex;
}

.movie-card .card-title {
  font-size: 1.2rem;
}

.movie-card .card-text {
  font-size: .9rem;
}

.movie-card a button {
  color: #29b6f6;
  background: none;
  border: none;
  width: 100%;
  padding: .5rem;
  transition: color .2s ease-in-out;
}

.movie-card a button:hover {
  color: #58d3f7;
}

@media (width <= 768px) {
  .movie-card {
    max-width: 250px;
  }
}

@media (width <= 576px) {
  .movie-card {
    max-width: 100%;
  }
}

.row {
  flex-wrap: wrap;
  justify-content: center;
  gap: 1rem;
  display: flex;
}

.movie-view {
  color: #fff;
  justify-content: center;
  align-items: flex-start;
  padding: 20px;
  display: flex;
}

.movie-view .movie-image {
  flex: 1;
  max-width: 25%;
  padding: 10px;
}

.movie-view .movie-image .movie-poster {
  border-radius: 8px;
  width: 100%;
  height: auto;
  max-height: 500px;
}

.movie-view .movie-details {
  flex: 2;
  max-width: 65%;
  padding: 20px;
}

.movie-view .movie-details .movie-title {
  margin-bottom: .5rem;
  font-size: 2.5rem;
}

.movie-view .movie-details .movie-metadata, .movie-view .movie-details .movie-director {
  margin-bottom: 1rem;
  font-size: 1.2rem;
}

.movie-view .movie-details .movie-description {
  margin-bottom: 2rem;
  font-size: 1rem;
}

.movie-view .back-button {
  color: #fff;
  cursor: pointer;
  background-color: #e50914;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
  font-size: 1rem;
  transition: background-color .3s;
}

.movie-view .back-button:hover {
  background-color: #b40710;
}

.signup-container {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 400px;
  padding: 2rem;
  display: flex;
}

.signup-container .form-group {
  width: 100%;
  margin-bottom: 1rem;
}

.signup-container .form-group label {
  margin-bottom: .5rem;
  display: block;
}

.signup-container .form-group input, .signup-container .form-group select, .signup-container .form-group textarea {
  background: #fffc;
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 100%;
  padding: .75rem;
}

.signup-container button {
  color: #fff;
  cursor: pointer;
  background-color: #0056b3;
  border-radius: 4px;
  width: 100%;
  padding: 1rem;
  font-size: 1rem;
  transition: background-color .3s;
}

.signup-container button:hover {
  background-color: #004494;
}

.navbar-custom {
  align-items: center;
  padding: 1rem 2rem;
  display: flex;
}

.navbar-custom .logo {
  color: #fff;
  flex: none;
  font-size: 1.8rem;
  font-weight: 700;
  text-decoration: none;
  transition: color .3s;
}

.navbar-custom .logo:hover {
  color: #f2aa4c;
}

.navbar-custom .nav {
  flex: 1;
  justify-content: center;
}

.navbar-custom .nav-link {
  color: #e0e0e0;
  border-radius: 4px;
  margin-right: 1rem;
  padding: .5rem 1rem;
  font-size: 1rem;
  font-weight: 500;
  text-decoration: none;
  transition: background-color .3s;
}

.navbar-custom .nav-link:hover {
  color: #fff;
  background-color: #292a32;
}

.navbar-custom .navbar-toggler {
  background-color: #0000;
  border: none;
  margin-left: auto;
  padding: 0;
}

.navbar-custom .navbar-toggler .navbar-toggler-icon {
  color: #fff;
  font-size: 1.5rem;
  transition: transform .3s;
}

.navbar-custom .navbar-toggler .navbar-toggler-icon:hover {
  transform: scale(1.1);
}

.home-container {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

.navigation {
  justify-content: center;
  gap: 20px;
  display: flex;
}

.navigation .nav-link {
  color: #fff;
  background-color: #fff3;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 1.5rem;
  text-decoration: none;
  transition: background-color .3s;
}

.navigation .nav-link:hover {
  background-color: #fff6;
}

.footer {
  text-align: center;
  color: #fff;
  width: 100%;
  position: absolute;
  bottom: 10px;
}

.button, .profile-form .btn-primary {
  color: #fff;
  cursor: pointer;
  background-color: #e50914;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
  font-size: 1rem;
  transition: background-color .3s;
}

.button:hover, .profile-form .btn-primary:hover {
  background-color: #b40710;
}

.profile-container {
  border-radius: 8px;
  padding: 2rem;
  box-shadow: 0 4px 8px #0000001a;
}

.profile-form {
  box-shadow: none;
  border-radius: 8px;
  padding: 2rem;
}

.profile-form h1 {
  color: #fff;
  margin-bottom: 1.5rem;
  font-size: 2rem;
}

.profile-form .form-group {
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: 1.5rem;
  display: flex;
}

.profile-form .form-label {
  color: #fff;
  flex: 0 0 25%;
  margin-bottom: .5rem;
  font-size: 1rem;
  font-weight: bold;
}

.profile-form .form-control {
  color: #fff;
  background-color: #0000;
  border: 1px solid #444;
  border-radius: 4px;
  flex: 0 0 75%;
  padding: .75rem;
  font-size: 1rem;
}

.profile-form .form-control:focus {
  border-color: #007bff;
  box-shadow: 0 0 0 .2rem #007bff40;
}

.profile-form .btn-primary {
  align-self: flex-end;
