}

.favorite-movies {
  box-shadow: none;
  border-radius: 8px;
  flex-wrap: nowrap;
  gap: 1rem;
  padding: 2rem;
  display: flex;
  overflow-x: scroll;
}

.favorite-movies h2 {
  color: #fff;
  margin-bottom: 1.5rem;
}

body {
  color: #fff;
  background: linear-gradient(to left, #000 0%, #350b0b 100%);
  height: 100vh;
  font-family: Arial, sans-serif;
}
